.container-news-item .container-img {
     /* width: 345px;
     height: 234px; */
     border-bottom: 20px solid white;
     position: relative;
     background-color: white;
     box-shadow: 10px 10px 20px #ccc;
     background-color: #ccc;
}
.container-news-item .container-img img {
     width: 100%;
     height: 100%;
}
.container-news-item .container-title .title {
     font-size: 16px;
     font-weight: 700;
     padding: 15px 0;
     cursor: pointer;
}
.container-news-item .container-title .title span:hover {
     color: var(--main-color);
}
.container-news-item .container-title .describe {
     display: block;
     display: -webkit-box;
     max-width: 100%;
     height: 50px;
     font-size: 14px;
     line-height: 24px;
     -webkit-box-orient: vertical;
     -webkit-line-clamp: 2;
     overflow: hidden;
     white-space: wrap;
     text-overflow: ellipsis;
}

